import axios from "axios";

const instance = axios.create({  //   axios   
   withCredentials: true,  //   cookie      
   baseURL: 'https://social-network.samuraijs.com/api/1.0/',
   headers: {
      "API-KEY": "760f4e50-1d38-43e0-a1cf-69f50254c676"  //  .delete  .post, ..  
   }
})

export const usersAPI = {  //     axios

   getUsers(currentPage = 1, pageSize = 10) {
      return instance.get(`users?page=${currentPage}&count=${pageSize}`)
         .then(response => {    //  .then(response => response.data)        response
            return response.data;  //  promise
         })
   },

   unfollow(usersId) {
      return instance.delete(`follow/${usersId}`)
   },

   follow(usersId) {
      return instance.post(`follow/${usersId}`, {}) //    {}   (  )
   },
   
   getProfile(userId) {
      return profileAPI.getProfile(userId) //,     , .. 
   }

}

export const profileAPI = {
   getProfile(userId) {
      return instance.get(`profile/` + userId);
   },
   getStatus(userId) {
      return instance.get(`profile/status/` + userId);
   },
   updateStatus(status) {
      return instance.put(`profile/status/`, {status: status})
   }
}

export const authAPI = {  //     axios
   me() {
      return instance.get(`auth/me`)
         //.then(response => response.data);  //  promise
   },
   login(email, password, rememberMe=false) {
      return instance.post(`auth/login`, {email, password, rememberMe});
   },
   logout() {
      return instance.delete(`auth/login`);
   }

}

